import { useEffect, useState } from "react"

export const Values = ({values}) => {

const {tower, floor, conference_room, pickedDate, time} = values
const [datePicked, setDate] = useState('')
const [timePicked, setTime] = useState('')

    function Date() {
        
        let year = pickedDate.substring(11, 15)
        let monthRaw = pickedDate.substring(4, 7)
        let day = pickedDate.substring(8, 10)

        let month = ''
        switch(monthRaw) {
            case 'Jan':
                month = ''
                break
            case 'Feb':
                month = ''
                break
            case 'Mar':
                month = ''
                break
            case 'Apr':
                month = ''
                break
            case 'May':
                month = ''
                break
            case 'Jun':
                month = ''
                break
            case 'Jul':
                month = ''
                break
            case 'Aug':
                month = ''
                break
            case 'Sep':
                month = ''
                break
            case 'Oct':
                month = ''
                break
            case 'Nov': 
                month = ''
                break
            case 'Dec':
                month = ''
                break
            default: 
                month = monthRaw
        }

        let regex = /^0\d/
        if (day.match(regex)) {
        day = day.slice(1)
        }
        
        let date = day + ' ' + month + ' ' + year + '.'
        return date
    }

    function Time() {
        let timePicked = time + ':00'
        return timePicked
    }

    useEffect(() => {
        setDate(Date())
        setTime(Time())
    }, [values])

    return (
        <div className='message_container'>
        <span>{`   ${conference_room}  ${floor}    ${tower}.   ${datePicked}  ${timePicked}.`}</span>
        </div>
    )
}